<template>
  <div class="wrapper">
      <el-container>
		<img class="one" src="@/assets/image/tourism/3.jpg" alt="" style="width: 100%;height: 85vh;">
		<el-main>
			<showPlay :play="play1"></showPlay>
			<div style="height: 20px;"></div>
			<pages 
				@current="current"
				:tatol="tatol"
			></pages>
		</el-main>
		<el-footer>
			<div style="height: 20px;"></div>
			<footers1></footers1>
			<div style="height: 100px;"></div>
		</el-footer>
      </el-container>
  </div>
</template>

<script>
	import footers1 from "@/components/footers.vue"
	import detailInfo from "@/components/detailInfo.vue"
	import showPlay from "@/components/showPlay.vue"
	import pages from "@/components/pages.vue"
	export default {
		components: {
			footers1,
			detailInfo,
			showPlay,
			pages
		},
		
		
		async beforeCreate() {
			// let that = this;
			await this.$A.get('/api/querytArticle')
			.then(res =>{
				let data = res.data.data.length
				this.tatol = Math.ceil(data/this.pageSize);
				this.queryPageSize()
			})
		},
		
		
		data() {
			return {
				play1:[],
				obj1:"",
				page:0,
				pageSize:4,
				tatol:9,
			}
		},
		methods: {
			async queryPageSize(){
			   await this.$A.get('/api/queryArticleByPage',{params: {page: this.page*this.pageSize,pageSize: this.pageSize}})
				  .then(res =>{
					this.obj1 = res.data.data
				  })
			   this.getplay()
			},
			getExecStrs(str) {
			       var reg = /<img src="(.+?)\">/g
			           // var reg = /\$\{(.+?)\}/g
			       var list = []
			       var result = null
			       do {
			           result = reg.exec(str)
			           result && list.push(result[1])
			       } while (result)
							  if(list.length<=0){
								  return;
							  }
			       return list
			},
			getplay(){
			   let news = this.obj1.map((r,i) =>{
				   return{
					   id:r.id,
					   url:this.getExecStrs(r.content)==undefined?' ':this.getExecStrs(r.content)[0] ,
					   title:r.title,
					   content:r.content,
					   time:new Date(parseInt(r.time)).toLocaleString()
				   }
			   })
			   this.play1 = news
			   // console.log(this.play1)
			},
			current(e){
			   if(e == 1){
				   if(e-1 == this.page){
						return;
				   }
				   
				   this.page = e-1
				   this.queryPageSize()
				   
			   }else{
				   if(e-1 == this.page){
					   return;
				   }
				   this.page = e-1
				   this.queryPageSize()
			   }
			}
		}
		
	}
</script>


<style>
	@media screen and (max-width:786px) {
		.one{
			height: 40vh !important;
		}
	}
</style>
